import { Component, keyframes, ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { CategoryService, PagerService, ProductService, ImageService } from '../_services/index';
import * as _ from 'underscore';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
declare var jquery: any;
declare var $: any;
declare var CKEDITOR: any;

import { AppSettings } from '../app.constant';
import { forEach } from '@angular/router/src/utils/collection';
import { element } from 'protractor';
import { BrandService } from './../_services/brand.service';

@Component({
    moduleId: module.id,
    templateUrl: 'create-product.component.html',
    styleUrls: ['create-product.component.css']
})

export class CreateProductComponent {
    images: FileList;
    categories: any = [];
    brands: any = [];
    product: any = {};
    description: string;
    progressUpload: any;
    listColor: any = ['#DD0F20'];
    listSize: any = [
        {
            Name: '', Price: '', PriceRetail: '', Quantity: '', QuantityWarning: ''
        }
    ];
    listImages: any = [];

    constructor(private router: Router, private categoryService: CategoryService,
        private pagerService: PagerService,
        private productService: ProductService, public toastr: ToastsManager, vcr: ViewContainerRef,
        private imagesService: ImageService, private brandService: BrandService) {
        this.description = 'M t cho sn phm';
        this.toastr.setRootViewContainerRef(vcr);
    }

    ngOnInit() {
        $('#uploading').hide();
        this.progressUpload = 0;
        this.loadAllBrandsForProduct();
        this.loadAllCatagories();
        $('#sizes').tagsinput();
        var timeOut = setTimeout(function () {
            CKEDITOR.replace('DescEditor', {
                language: 'vi'
            });
            $('#cp-0').colorpicker();
        }, 1000);
    }
    private loadAllBrandsForProduct() {
        this.brandService.getAllForProduct().subscribe(data => {
            this.brands = data;
            $('#select2-brand').select2();
        });
    }

    private loadAllCatagories() {
        this.categoryService.getAllForProduct().subscribe(data => {
            this.categories = data.Data;
            $('#select2-group').select2();
        });
    }

    createProduct() {
        var selectedCate = $("#select2-group").val();
        var selectedBrand = $("#select2-brand").val();
        // get colors
        var colors = [];
        this.listColor.forEach((item, index) => {
            var color = $('#cp-' + index + ' input').val();
            colors.push(color);
        });
        $('#uploading').show();
        var content = CKEDITOR.instances.DescEditor.getData();
        this.product.Description = $('<div/>').text(content).html();
        this.product.Description = this.product.Description.replace(/\s{2,}/g, '');
        this.product.CategoryId = selectedCate;
        this.product.BrandId = selectedBrand;
        this.product.Sizes = $('#sizes').val();
        this.product.Colors = colors.join();
        this.product.Sizes = this.listSize;
        this.product.Images = this.listImages;
        this.productService.create(this.product).subscribe(res => {
            this.toastr.success('To sn phm thnh cng!', 'Thnh cng!');
            this.router.navigate(['/products']);
        });
    }

    onChange(event: EventTarget) {
        let eventObj: MSInputMethodContext = <MSInputMethodContext>event;
        let target: HTMLInputElement = <HTMLInputElement>eventObj.target;
        let files: FileList = target.files;
        this.images = files;
        // call api upload images
        var formData: FormData = new FormData();
        for (var i = 0; i < this.images.length; i++) {
            formData.append('files', this.images[i], this.images[i].name);
        }
        var xhr = new XMLHttpRequest();
        xhr.onreadystatechange = () => {
            if (xhr.readyState === 4) {
                if (xhr.status === 200) {
                    var images = JSON.parse(xhr.response);
                    images.forEach(element => {
                        element.Path = AppSettings.API_ENDPOINT_IMAGE + element.Path.substr(1);
                        this.listImages.push(element);
                    });
                } else {

                }
            }
        };

        xhr.upload.onprogress = (event) => {
            this.progressUpload = Math.round(event.loaded / event.total * 100);
            $('#progess-load').css('width', this.progressUpload + '%');
        };

        // xhr.setRequestHeader('Authorization', localStorage.token_type + ' ' + localStorage.access_token);

        xhr.open("POST", AppSettings.API_ENDPOINT + 'products/UploadImagesToPreview', true);
        xhr.send(formData);
    }

    addColor() {
        this.listColor.push('#000000');
        var newPosition = this.listColor.length - 1;
        setTimeout(function () {
            $('#cp-' + newPosition).colorpicker();
        }, 100);
    }


    removeColor(index) {
        if (this.listColor.length !== 1) {
            this.listColor.splice(index, 1);
        }
    }

    addSize() {
        this.listSize.push({
            Name: '', Price: '', PriceRetail: '', Quantity: '', QuantityWarning: ''
        });
    }

    removeSize(index) {
        if (this.listSize.length !== 1) {
            this.listSize.splice(index, 1);
        }
    }


    removeImage(id) {
        this.imagesService.deleteImage(id).subscribe(data => {
            if (data.status == 200) {
                // remove 
                var index = this.findIndexImage(id);
                if (index != -1) {
                    this.listImages.splice(index);
                }
            }
        });
    }

    findIndexImage(id) {
        var index = -1;
        this.listImages.forEach((item, i) => {
            if (item.Id === id) {
                index = i;
            }
        });
        return index;
    }

}



