import { Component, ViewContainerRef } from '@angular/core';
import { element } from 'protractor';
import { Router } from '@angular/router';
import { LocalStorageService } from './_services/localstorage.service';
import { Subscription } from 'rxjs';
import { Location } from '@angular/common';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { AuthenticationService } from './_services';
import { ToastsManager } from 'ng2-toastr';

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.css']
})
export class AppComponent {
	title = 'app';
	subscription: Subscription;
	currentUrl = '';
	accountLogged: any = {};
	isLogged: boolean = false;
	route: string;
	template: string = `<img src="../assets/images/icon_loading.gif" />`;
	constructor(private router: Router, private storageService: LocalStorageService, location: Location,
		private authenticationService: AuthenticationService,
		public toastr: ToastsManager, vcr: ViewContainerRef,
		private spinnerService: Ng4LoadingSpinnerService) {

		this.toastr.setRootViewContainerRef(vcr);
		this.subscription = this.storageService.watchStorage().subscribe(message => {
			this.init();
		});
	}

	init() {
		var token = localStorage.getItem('access_token');
		if (token) {
			this.spinnerService.show();
			this.getInfoUser();
		}
		else {
			this.router.navigate(['/login']);
		}
	}

	ngOnInit() {
		this.init();
	}

	ngOnDestroy() {
		// unsubscribe to ensure no memory leaks
		this.subscription.unsubscribe();
	}

	menus = [
		{
			main_icon: 'fa fa-home',
			title: 'Loi sn phm',
			isActive: true,
			url: '',
			children: [
				{
					title: 'Loi sn phm',
					url: '/categories',
					isActive: true
				},
				{
					title: 'Nhm loi sn phm',
					url: '/group-categories',
					isActive: false
				}
			]
		},
		{
			main_icon: 'fa fa-home',
			title: 'Sn phm',
			isActive: false,
			url: '',
			children: [
				{
					title: 'Danh sch sn phm',
					url: '/products',
					isActive: false
				},
				{
					title: 'To sn phm',
					url: '/create-product',
					isActive: false
				}
			]
		},
		{
			main_icon: 'fa fa-home',
			title: 'Nhn hiu',
			url: 'product-brand',
			isActive: false,
			children: []
		}
	];

	setActiveRoute() {
		this.menus.forEach((element, index) => {
			element.children.forEach((child, indexChild) => {
				if (this.currentUrl === child.url) {
					child.isActive = true;
					element.isActive = true;
				} else {
					child.isActive = false;
					element.isActive = false;
				}
			});
		});
	}

	onClickMenu(index) {
		this.menus.forEach((element, i) => {
			if (index === i) {
				element.isActive = true;
			} else {
				element.isActive = false;
			}
		});
	}

	private getInfoUser() {
		this.authenticationService.getProfile().subscribe(
			response => {
				this.accountLogged = response;
				this.isLogged = true;
				this.spinnerService.hide();
			}, error => {
				this.router.navigate(['/login']);
				console.log(error.json());
				this.toastr.error(error.json().Message);
				this.spinnerService.hide();
			}
		);
	}

	gotoUrl(index, iChild) {
		this.menus.forEach((element, i) => {
			if (index === i) {
				element.isActive = true;
				element.children.forEach((child, indexChild) => {
					if (iChild === indexChild) {
						child.isActive = true;
					} else {
						child.isActive = false;
					}
				});
			} else {
				element.children.forEach((child, indexChild) => {
					child.isActive = false;
				});
			}
		});
	}

}



