import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CategoryService, PagerService, ProductService, SaleService } from '../_services/index';
import { ActivatedRoute } from '@angular/router';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import * as _ from 'underscore';
declare var jquery: any;
declare var $: any;
import { AppSettings } from '../app.constant';
import { forEach } from '@angular/router/src/utils/collection';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';

@Component({
    selector: 'app-sale-management',
    templateUrl: './sale-management.component.html',
    styleUrls: ['./sale-management.component.css']
})
export class SaleManagementComponent implements OnInit {
    private id: any;
    products: any = [];
    sort: string; page: any; amount: any;
    category: any = {};
    nameBtnCreate: string;
    allDatas: any = {};
    pager: any = {};
    pagedItems: any = [];
    keysearch: string;
    gopage: string;

    productsAdded: any = [];

    constructor(private router: Router, private categoryService: CategoryService,
        private pagerService: PagerService, private saleService: SaleService,
        private productService: ProductService, private slimLoadingBarService: SlimLoadingBarService,
        private route: ActivatedRoute, public toastr: ToastsManager) {
        this.sort = 'Name';
        this.page = 0;
        this.amount = 10;
        this.keysearch = '';
        this.pagedItems = [];
        this.productsAdded = [];
    }

    ngOnInit() {
        this.id = this.route.snapshot.params['id'];
        this.getSaleProgram(this.id);
    }

    getSaleProgram(id: any) {
        this.saleService.getById(id).subscribe(data => {
            data.Products.forEach(function (e: any) {
                e.ProductImages.forEach(function (image: any) {
                    image.Path = AppSettings.API_ENDPOINT_IMAGE + image.Path.substr(1);
                });
            });
            this.productsAdded = data.Products;
            this.loadAllProducts(this.sort, this.page, this.amount, this.keysearch);
        });
    }

    removeProduct($event: any) {
        if (!this.containsObject($event.dragData, this.pagedItems)) {
            this.pagedItems.push($event.dragData);

            this.productsAdded = this.productsAdded.filter(function (element) {
                return element.Id !== $event.dragData.Id;
            });
        }
    }

    addProduct($event: any) {
        if (!this.containsObject($event.dragData, this.productsAdded)) {
            this.productsAdded.push($event.dragData);
            this.pagedItems = this.pagedItems.filter(function (element) {
                return element.Id !== $event.dragData.Id;
            });
        }
    }

    private loadAllProducts(sort: string, page: any, amount: any, keysearch: any) {
        this.slimLoadingBarService.start(() => {
            console.log('Loading complete');
        });
        this.productService.getAll(sort, page, amount, keysearch).subscribe(data => {
            data.Data.forEach(function (e: any) {
                e.ProductImages.forEach(function (image: any) {
                    image.Path = AppSettings.API_ENDPOINT_IMAGE + image.Path.substr(1);
                });
            });
            console.log(this.productsAdded);
            data.Data.forEach(function(e){
                if(!this.containsObject(e, this.productsAdded)){
                    this.products.push(e);
                    this.pagedItems.push(e);
                }
            });
            this.allDatas = data;
            this.pager = this.pagerService.getPager(this.allDatas.TotalPage, this.allDatas.TotalRecord, page, this.amount);
            this.page = page;
            this.slimLoadingBarService.complete();
            // check 
        });
    }

    saveAddedProducts() {
        /// get Products Id
        var productIds = [];
        this.productsAdded.forEach(function (e) {
            productIds.push(e.Id);
        });

        var objectAdd = {
            SaleId: this.id,
            ProductsId: productIds
        }
        console.log(objectAdd);
        this.saleService.addProductsToSale(objectAdd).subscribe(data => {
            this.toastr.success('Thm sn phm vo chng trnh Sale!', 'Thnh Cng!');
        });
    }

    setPage(page: number) {
        if (page < 1 || page > this.pager.totalPages) {
            return;
        }
        console.log(this.keysearch)
        this.loadAllProducts(this.sort, page - 1, this.amount, this.keysearch);
    }


    detailProduct(id: string) {
        this.router.navigateByUrl('/product-detail/' + id);
    }

    DeteleCategory(id: any) {
        this.categoryService.delete(id).subscribe(data => {
            this.loadAllProducts(this.sort, this.page, this.amount, this.keysearch);
        });
    }

    gotoPage() {
        if (this.gopage != "") {
            this.setPage(+this.gopage);
        } else {
            this.setPage(1);
        }
    }

    goSearch() {
        this.setPage(1);
    }

    containsObject(obj: any, list: any) {
        var i;
        for (i = 0; i < list.length; i++) {
            if (list[i] === obj) {
                return true;
            }
        }
        return false;
    }

}



