import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/index';
import { LoginComponent } from './login/index';
import { AuthGuard } from './_guards/index';
import { CategoriesComponent } from './categories/index';
import { ProductsComponent } from './products/index';
import { CreateProductComponent } from './create-product/index';
import { ProductDetailComponent } from './product-detail/index';
import { SaleProgramsComponent } from './sale-programs/sale-programs.component';
import { SaleManagementComponent } from './sale-management/sale-management.component';
import { GroupCategoriesComponent } from './group-categories/group-categories.component';
import { ProductBrandComponent } from './product-brand/product-brand.component';


const appRoutes: Routes = [
    { path: '', component: HomeComponent },
    { path: 'login', component: LoginComponent },
    { path: 'categories', component: CategoriesComponent },
    { path: 'products', component: ProductsComponent },
    { path: 'create-product', component: CreateProductComponent },
    { path: 'product-detail/:id', component: ProductDetailComponent },
    { path: 'sale-programs', component: SaleProgramsComponent },
    { path: 'sale-management/:id', component: SaleManagementComponent },
    { path: 'group-categories', component: GroupCategoriesComponent },
    { path: 'product-brand', component: ProductBrandComponent },
    // otherwise redirect to home
    { path: '**', redirectTo: '' }
];

export const routing = RouterModule.forRoot(appRoutes);


