import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { AppSettings } from '../app.constant'
import 'rxjs/add/operator/map'

@Injectable()
export class AuthenticationService {
    constructor(private http: Http) { }

    login(data: {}) {
        return this.http.post(AppSettings.API_ENDPOINT + 'Accounts/token', this.formatData(data))
            .map((response: Response) => {
                let tokenObject = response.json();
                return tokenObject;
            });
    }

    logout() {
        // remove user from local storage to log user out
        localStorage.removeItem('currentUser');
    }

    private formatData(data: any){
        let returnData = '';
        let count = 0;
        for (let i in data){
            if(count == 0){
                returnData += i+'='+data[i];
            }else{
                returnData += '&'+i+'='+data[i];
            }
            count = count + 1;
        }
        return returnData;
    }

    getProfile() {
        return this.http.get(AppSettings.API_ENDPOINT + 'Accounts/GetProfile', this.jwt()).map((response: Response) => response.json());
    }

    private jwt() {
        let headers = new Headers({
            'Content-Type': 'application/json',
            'authorization': localStorage.token_type + ' ' + localStorage.access_token
        });
        return new RequestOptions({ headers: headers });
    }

}


