import { Component, keyframes, ViewContainerRef, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SaleService, PagerService } from '../_services/index';
import * as _ from 'underscore';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
declare var jquery: any;
declare var $: any;

import { AppSettings } from '../app.constant';
import { LocalStorageService } from '../_services/localstorage.service';

@Component({
    selector: 'app-sale-programs',
    templateUrl: './sale-programs.component.html',
    styleUrls: ['./sale-programs.component.css']
})
export class SaleProgramsComponent implements OnInit {
    sales: any = [];
    sort: string; page: any; amount: any;
    sale: any = {};
    nameBtnCreate: string;
    idUpdate: any;
    isUpdating: boolean;
    allDatas: any = {};
    pager: any = {};
    pagedItems: any[];
    keysearch: string;
    gopage: string;
    images: FileList;

    constructor(private router: Router, private saleService: SaleService,
        private pagerService: PagerService, private localStorageService: LocalStorageService,
        public toastr: ToastsManager, vcr: ViewContainerRef) {
        this.toastr.setRootViewContainerRef(vcr);
        this.sort = 'Name';
        this.page = 0;
        this.amount = 10;
        this.nameBtnCreate = 'To';
        this.isUpdating = false;
        this.keysearch = '';
    }

    ngOnInit() {
        $('#btn-cancel').hide();
        // $('#timestart').datetimepicker();
        // $('#timeend').datetimepicker();
        this.loadAllSales(this.sort, this.page, this.amount, this.keysearch);
        let currentUrl = this.router.url;
    }

    private loadAllSales(sort: string, page: any, amount: any, keysearch: any) {
        this.saleService.getAll(sort, page, amount, keysearch).subscribe(data => {
            this.sales = data.Data;
            this.pagedItems = data.Data;
            this.allDatas = data;
            this.pager = this.pagerService.getPager(this.allDatas.TotalPage, this.allDatas.TotalRecord, page, this.amount);
            this.page = page;
        });
    }

    setPage(page: number) {
        if (page < 1 || page > this.pager.totalPages) {
            return;
        }
        this.loadAllSales(this.sort, page - 1, this.amount, this.keysearch);
    }

    CreateSale() {
        if (this.isUpdating) {
            this.sale.Id = this.idUpdate;
            this.saleService.update(this.sale).subscribe(data => {
                this.loadAllSales(this.sort, this.page, this.amount, this.keysearch);
                this.sale.Name = '';
                this.sale.Description = '';
                this.isUpdating = false;
                this.nameBtnCreate = 'To';
                $('#btn-cancel').hide();
                this.toastr.success('Cp nht loi sale thnh cng!', 'Thnh Cng!');
            });
        } else {
            this.sale.ProductIds = [];
            this.sale.TimeStart = $('#timestart').val();
            this.sale.EndTime = $('#timeend').val();
            var formData: FormData = new FormData();
            for (var i = 0; i < this.images.length; i++) {
                formData.append('files', this.images[i], this.images[i].name);
            }
            formData.append('objCreateSaleModel', JSON.stringify(this.sale));
            var xhr = new XMLHttpRequest();
            xhr.onreadystatechange = () => {
                if (xhr.readyState === 4) {
                    if (xhr.status === 200) {
                        this.toastr.success('To chng trnh khuyn mi thnh cng!', 'Thnh cng!');
                        this.loadAllSales(this.sort, this.page, this.amount, this.keysearch);
                    } else {
                        this.toastr.error('Li trong qu trnh to chng trnh!', 'Oops!');
                    }
                }
            };
            console.log(this.sale);
            xhr.open("POST", AppSettings.API_ENDPOINT + 'sales/Create', true);
            xhr.send(formData);
        }
    }

    UpdateSale(item: any) {
        this.sale.Name = item.Name;
        this.sale.Description = item.Description;
        this.nameBtnCreate = 'Sa';
        this.idUpdate = item.Id;
        this.isUpdating = true;
        $('#btn-cancel').show();
    }

    cancelUpdate() {
        this.sale.Name = '';
        this.sale.Description = '';
        this.nameBtnCreate = 'To';
        this.idUpdate = '';
        this.isUpdating = false;
        $('#btn-cancel').hide();
    }

    seeProducts(id: any){
        this.router.navigateByUrl('/sale-management/' + id);
    }

    DeteleSale(id: any) {
        this.saleService.delete(id).subscribe(data => {
            this.loadAllSales(this.sort, this.page, this.amount, this.keysearch);
            this.toastr.info('Xa loi sale thnh cng!', 'Thnh Cng!');
        });
    }

    gotoPage() {
        if (this.gopage != "") {
            this.setPage(+this.gopage);
        } else {
            this.setPage(1);
        }
    }

    goSearch() {
        this.setPage(1);
    }

    onChange(event: EventTarget) {
        let eventObj: MSInputMethodContext = <MSInputMethodContext>event;
        let target: HTMLInputElement = <HTMLInputElement>eventObj.target;
        let files: FileList = target.files;
        this.images = files;
    }

}



