import { Component, keyframes, ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { CategoryService, PagerService } from '../_services/index';
import * as _ from 'underscore';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { LocalStorageService } from './../_services/localstorage.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import Swal from 'sweetalert2'
declare var jquery: any;
declare var $: any;

@Component({
    moduleId: module.id,
    templateUrl: 'categories.component.html',
    styleUrls: ['categories.component.css']
})

export class CategoriesComponent {
    categories: any = [];
    sort: string; page: any; amount: any;
    totalPage: any = 1;
    totalRecord: 10;
    category: any = {};
    nameBtnCreate: string;
    idUpdate: any;
    isUpdating: boolean;
    allDatas: any = {};
    pager: any = {};
    pagedItems: any[];
    keysearch: string;
    gopage: string;
    allGroup = [];
    idGroup = '';

    constructor(private router: Router, private categoryService: CategoryService,
        private pagerService: PagerService, private localStorageService: LocalStorageService,
        public toastr: ToastsManager, vcr: ViewContainerRef,
        private spinnerService: Ng4LoadingSpinnerService) {
        this.toastr.setRootViewContainerRef(vcr);
        this.sort = 'Name';
        this.page = 0;
        this.amount = 10;
        this.nameBtnCreate = 'To';
        this.isUpdating = false;
        this.keysearch = '';
    }

    ngOnInit() {
        $('#btn-cancel').hide();
        this.loadAllGroupCategories();
        this.loadAllCategories(this.sort, this.page, this.amount, this.keysearch);
        let currentUrl = this.router.url;
    }

    loadAllGroupCategories() {
        this.categoryService.getAllGroupForCategory().subscribe(data => {
            this.allGroup = data;
            $('#select2-group').select2();
        });
    }

    loadAllCategories(sort: string, page: any, amount: any, keysearch: any) {
        this.categoryService.getAll(sort, page, amount, keysearch).subscribe(data => {
            this.categories = data.Data;
            this.pagedItems = data.Data;
            this.amount = data.Amount;
            this.totalPage = data.TotalPage;
            this.totalRecord = data.TotalRecord;
            this.allDatas = data;
            this.pager = this.pagerService.getPager(this.allDatas.TotalPage, this.allDatas.TotalRecord, page, this.amount);
            this.page = page;
        });
    }

    setPage(page: number) {
        if (page < 1 || page > this.pager.totalPages) {
            return;
        }
        this.loadAllCategories(this.sort, page - 1, this.amount, this.keysearch);
    }

    CreateCategory() {
        var selectedCates = $("#select2-group").val();
        this.category.GroupId = selectedCates;
        this.spinnerService.show();
        if (this.isUpdating) {
            this.category.Id = this.idUpdate;
            this.categoryService.update(this.category).subscribe(data => {
                this.category.Name = '';
                this.category.Description = '';
                this.isUpdating = false;
                this.nameBtnCreate = 'To';
                $('#btn-cancel').hide();
                this.spinnerService.hide();
                this.loadAllCategories(this.sort, this.page, this.amount, this.keysearch);
                this.toastr.success('Cp nht loi sn phm thnh cng!', 'Thnh Cng!');
            });
        } else {
            this.categoryService.create(this.category).subscribe(data => {
                this.category.Name = '';
                this.category.Description = '';
                this.spinnerService.hide();
                this.loadAllCategories(this.sort, this.page, this.amount, this.keysearch);
                this.toastr.success('To loi sn phm thnh cng!', 'Thnh Cng!');
            });
        }
    }

    UpdateCategory(item: any) {
        this.category.Name = item.Name;
        this.category.Description = item.Description;
        this.nameBtnCreate = 'Sa';
        this.idUpdate = item.Id;
        this.isUpdating = true;
        $('#select2-group').val(item.groupCategory.Id).trigger('change');
        $('#btn-cancel').show();
    }

    cancelUpdate() {
        this.category.Name = '';
        this.category.Description = '';
        this.nameBtnCreate = 'To';
        this.idUpdate = '';
        this.isUpdating = false;
        $('#btn-cancel').hide();
    }

    DeteleCategory(id: any) {
        Swal({
            title: 'Xc nhn xa?',
            text: "Khi xa s khng khi phc c!",
            type: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            cancelButtonText: 'Hy',
            confirmButtonText: 'Vng, Xa n i!'
        }).then((result) => {
            if (result.value) {
                this.categoryService.delete(id).subscribe(data => {
                    if (this.categories.length == 1) {
                        this.page = this.page - 1;
                    }
                    this.loadAllCategories(this.sort, this.page, this.amount, this.keysearch);
                    Swal(
                        ' xa!',
                        'Loi sn phm  xa thnh cng.',
                        'success'
                    )
                });
            }
        });
    }

    gotoPage() {
        if (this.gopage != "") {
            this.setPage(+this.gopage);
        } else {
            this.setPage(1);
        }
    }

    goSearch() {
        this.setPage(1);
    }

}



