import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { Routes, RouterModule } from '@angular/router';
// import { TagInputModule } from 'ng2-tag-input';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { DndModule } from 'ng2-dnd';

import { routing } from './app.routing';

// service
import {
	AuthenticationService, CategoryService, PagerService,
	ProductService, SaleService, ImageService, BrandService
} from './_services/index';

import { CKEditorModule } from 'ng2-ckeditor';
import { ToastModule } from 'ng2-toastr/ng2-toastr';
import { SlimLoadingBarModule } from 'ng2-slim-loading-bar';
import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';

import { AuthGuard } from './_guards/index';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/index';
import { LoginComponent } from './login/index';
import { CategoriesComponent } from './categories/index';
import { ProductsComponent } from './products/index';
import { CreateProductComponent } from './create-product/index';
import { ProductDetailComponent } from './product-detail/index';
import { SaleProgramsComponent } from './sale-programs/sale-programs.component';
import { SaleManagementComponent } from './sale-management/sale-management.component';
import { LocalStorageService } from './_services/localstorage.service';
import { GroupCategoriesComponent } from './group-categories/group-categories.component';
import { ProductBrandComponent } from './product-brand/product-brand.component';


@NgModule({
	declarations: [
		AppComponent,
		HomeComponent,
		LoginComponent,
		CategoriesComponent,
		ProductsComponent,
		CreateProductComponent,
		ProductDetailComponent,
		SaleProgramsComponent,
		SaleManagementComponent,
		GroupCategoriesComponent,
		ProductBrandComponent
	],
	imports: [
		BrowserModule,
		FormsModule,
		HttpModule,
		routing,
		CKEditorModule,
		BrowserAnimationsModule,
		ToastModule.forRoot(),
		SlimLoadingBarModule.forRoot(),
		DndModule.forRoot(),
		Ng4LoadingSpinnerModule.forRoot()
	],
	providers: [
		AuthGuard,
		AuthenticationService,
		CategoryService,
		PagerService,
		ProductService,
		SaleService,
		LocalStorageService,
		ImageService,
		BrandService
	],
	bootstrap: [AppComponent]
})
export class AppModule { }



