import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { AppSettings } from '../app.constant';

@Injectable()
export class SaleService {
    constructor(private http: Http) { }

    getAll(sort: string, page: any, amount: any, keysearch: string) {
        return this.http.get(AppSettings.API_ENDPOINT + 'sales/GetAll?sort=' + sort + '&page=' + page + '&amount=' + amount + '&keysearch=' + keysearch)
        .map((response: Response) => response.json());
    }

    getById(id: any){
        return this.http.get(AppSettings.API_ENDPOINT + 'sales/getById?id=' + id)
        .map((response: Response) => response.json());
    }

    addProductsToSale(products){
        return this.http.post(AppSettings.API_ENDPOINT +'sales/AddProductsToSale', products, this.jwt()).map((response: Response) => response.json());
    }

    create(sale: any) {
        return this.http.post(AppSettings.API_ENDPOINT +'sales/Create', sale, this.jwt()).map((response: Response) => response.json());
    }

    update(sale: any) {
        return this.http.put(AppSettings.API_ENDPOINT +'sales/Update', sale, this.jwt()).map((response: Response) => response.json());
    }

    delete(id: any) {
        return this.http.delete(AppSettings.API_ENDPOINT +'sales/Delete?Id=' + id, this.jwt()).map((response: Response) => response);
    }

    // private helper methods

    private jwt() {
        let headers = new Headers({ 
            'Content-Type' : 'application/json'
        });
        return new RequestOptions({ headers: headers });
    }
}


